/*@import "variables";*/
/* -------------------------------------Body ----------------------------------------*/
html, body {
  margin: 0;
  padding: 0;
  /*font-family: Arial, Helvetica, sans-serif;*/
  text-align: left;
  text-decoration: none;
  text-transform: none;
}

body * {
  outline: none;
  box-sizing: border-box;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

*:focus {
  outline: none;
}

a {
  color: #000;
  text-decoration: none;
  cursor: pointer;
  outline: none;
}

a:hover, a:focus, a img:hover, a:active {
  text-decoration: none;
  outline: none;
}

a img {
  border: none;
}

h1, h2, h3, h4, h5, h6 {
  margin: 0;
  padding: 0;
}

ul {
  padding: 0 0 0 20px;
  margin: 0;
}

li {
  margin: 10px 0;
}

form {
  margin: 0;
  padding: 0;
  display: block;
}

.left {
  float: left;
}

.right {
  float: right;
}

.clear {
  clear: both;
}

.clearLeft {
  clear: left;
}

.clearRight {
  clear: right;
}

.clearfix {
  zoom: 1;
  *display: inline-block;
}

.clearfix:after {
  content: ".";
  display: block;
  clear: both;
  width: 0px;
  height: 0px;
  visibility: hidden;
  font-size: 0;
}

.hide {
  display: none !important;
}

.row.no-gutter {
  width: 100%;
  margin-left: 0;
  margin-right: 0;
}

.row.no-gutter [class*='col-'] {
  padding-right: 0;
  padding-left: 0;
}

.noscrolling {
  overflow: hidden;
  height: 100%;
}

.noscrolling body {
  overflow: hidden;
  position: relative;
  height: 100%;
  -ms-touch-action: none;
  touch-action: none;
}

.preload * {
  transition: none !important;
}

@font-face {
  font-family: 'icomoon';
  src: url("fonts/icomoon.eot?vnzmn4");
  src: url("fonts/icomoon.eot?vnzmn4#iefix") format("embedded-opentype"), url("fonts/icomoon.ttf?vnzmn4") format("truetype"), url("fonts/icomoon.woff?vnzmn4") format("woff"), url("fonts/icomoon.svg?vnzmn4#icomoon") format("svg");
  font-weight: normal;
  font-style: normal;
}

[class^="icon-"], [class*=" icon-"] {
  /* use !important to prevent issues with browser extensions that change fonts */
  font-family: 'icomoon' !important;
  speak: none;
  font-style: normal;
  font-weight: normal;
  font-variant: normal;
  text-transform: none;
  line-height: 1;
  /* Better Font Rendering =========== */
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

.icon-keyboard_arrow_down:before {
  content: "\e313";
}

.icon-keyboard_arrow_left:before {
  content: "\e314";
}

.icon-keyboard_arrow_right:before {
  content: "\e315";
}

.icon-keyboard_arrow_up:before {
  content: "\e316";
}

html {
  font-size: 16px;
}

@media (min-width: 992px) {
  html {
    font-size: 24px;
  }
}

body {
  background: #ffffff;
  line-height: 1.2;
}

.wrapper {
  overflow-x: hidden;
  position: relative;
  min-height: 100vh;
}

header {
  background: #ea491c;
  padding: 10px 20px;
  height: 70px;
}

header .logo {
  display: inline-block;
  width: 100px;
}

@media (min-width: 992px) {
  header .logo {
    width: 230px;
  }
}

header .menu ul li {
  display: inline-block;
  margin: 0 0 0 30px;
  font-size: 28px;
}

header .menu ul li a {
  color: #000;
  padding: 10px 0;
  transition: opacity .5s;
}

header .menu ul li a:hover {
  color: #000;
  opacity: .7;
}

header .menu ul li .login-btn {
  background: #ea4d17;
  color: #fff;
  font-weight: bold;
  padding: 10px 30px;
  border-radius: 1em;
}

header .menu ul li .login-btn:hover {
  opacity: .7;
  color: #fff;
}

@media (min-width: 992px) {
  header {
    background: #f2f2f2;
    padding: 26px 0;
    height: auto;
  }
}

header .mobile-menu .mobile-menu-trigger {
  display: inline-block;
  height: 24px;
}

header .mobile-menu .mobile-menu-trigger.opened .mobile-button span {
  opacity: 0;
}

header .mobile-menu .mobile-menu-trigger.opened .mobile-button:before {
  top: 10px;
  transform: rotate(45deg);
}

header .mobile-menu .mobile-menu-trigger.opened .mobile-button:after {
  top: 10px;
  transform: rotate(-45deg);
}

header .mobile-menu .mobile-button {
  position: relative;
  display: block;
  width: 24px;
  height: 24px;
}

header .mobile-menu .mobile-button:before, header .mobile-menu .mobile-button:after, header .mobile-menu .mobile-button span {
  content: "";
  position: absolute;
  display: block;
  left: 0;
  transition: all .25s ease;
  width: 100%;
  height: 4px;
  background: #fff;
}

header .mobile-menu .mobile-button span {
  top: 10px;
}

header .mobile-menu .mobile-button:before {
  top: 0;
}

header .mobile-menu .mobile-button:after {
  top: 20px;
}

